/* element objects */
.css_set_editor_field .css_set_editor_body .css_set_editor_element_objects {
  position: relative;
  width: 100%;
}

/* element objects > element object */
.css_set_editor_field .css_set_editor_body .css_set_editor_element_objects .css_set_editor_element_object {
  position: relative;
  width: 100%;
  height: 65px;
  margin-top: 5px;
  background: var(--d-cssvar-ffffff);
}

.css_set_editor_field .css_set_editor_body .css_set_editor_element_objects .css_set_editor_element_object:last-child {
  margin-bottom: calc(5px + var(--d-cssvar-safe-area-bottom));
}

/* element objects > element object > thumbnail */
.css_set_editor_field .css_set_editor_body .css_set_editor_element_objects .css_set_editor_element_object .css_set_editor_element_object_thumbnail {
  position: absolute;
  top: 10px;
  left: 10px;
  width: calc(65px - 10px * 2);
  height: calc(65px - 10px * 2);
  border-radius: var(--d-cssvar-border-radius-large);
  background: var(--d-cssvar-ededf0);
  user-select: none;
  -webkit-user-select: none;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.css_set_editor_field .css_set_editor_body .css_set_editor_element_objects .css_set_editor_element_object .css_set_editor_element_object_thumbnail span.css_material_symbols_rounded {
  height: 20px;
  width: 20px;
  color: var(--d-cssvar-000408);
}

.css_set_editor_field .css_set_editor_body .css_set_editor_element_objects .css_set_editor_element_object .css_set_editor_element_object_thumbnail img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  user-select: none;
  -webkit-user-select: none;
  -webkit-user-drag: none;
}

/* element objects > element object > context */
.css_set_editor_field .css_set_editor_body .css_set_editor_element_objects .css_set_editor_element_object .css_set_editor_element_object_context {
  position: absolute;
  top: 10px;
  left: 65px;
  height: calc(65px - 10px * 2);
  width: calc(100% - 65px - 55px);
  display: flex;
  align-items: center;
  font-size: 20px;
  color: var(--d-cssvar-000408);
  overflow: hidden;
  word-break: keep-all;
  white-space: nowrap;
  user-select: none;
  -webkit-user-select: none;
}

/* element objects > element object > edit */
.css_set_editor_field .css_set_editor_body .css_set_editor_element_objects .css_set_editor_element_object .css_set_editor_element_object_edit {
  position: absolute;
  top: 0px;
  right: 0px;
  height: 65px;
  width: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.css_set_editor_field .css_set_editor_body .css_set_editor_element_objects .css_set_editor_element_object .css_set_editor_element_object_edit span.css_material_symbols_rounded {
  width: 20px;
  height: 20px;
  color: var(--d-cssvar-000408);
}